@tailwind base;
@tailwind components;
@tailwind utilities;
/* Global styles */
body {
  @apply bg-secondary-50 text-secondary-900;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
}

/* Custom components */
.btn-primary {
  @apply bg-primary-600 hover:bg-primary-700 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200;
}

.btn-secondary {
  @apply bg-secondary-200 hover:bg-secondary-300 text-secondary-800 font-medium py-2 px-4 rounded-lg transition-colors duration-200;
}

.card {
  @apply bg-white rounded-lg shadow-md p-6 border border-secondary-200;
}

.input-field {
  @apply w-full px-3 py-2 border border-secondary-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent;
}

/* Minecraft-themed elements */
.minecraft-button {
  @apply bg-minecraft-green text-black font-bold py-2 px-4 border-2 border-black;
  font-family: "Courier New", monospace;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.5);
}

.minecraft-card {
  @apply bg-secondary-800 border-2 border-secondary-600 text-white;
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.1) 25%, transparent 25%), linear-gradient(-45deg, rgba(255, 255, 255, 0.1) 25%, transparent 25%), linear-gradient(45deg, transparent 75%, rgba(255, 255, 255, 0.1) 75%), linear-gradient(-45deg, transparent 75%, rgba(255, 255, 255, 0.1) 75%);
  background-size: 4px 4px;
  background-position: 0 0, 0 2px, 2px -2px, -2px 0px;
}
